import React from 'react';
import { Link } from 'react-router-dom';
import { getData, getItem } from '../../js/script';

class Cart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cart: [],
    };
  }

  componentDidMount() {
    if (!localStorage.getItem('cartId')) {
      return null;
    }
    getData(`cart/${localStorage.getItem('cartId')}`).then(response =>
      this.getCartItems(response.data.products)
    );
  }

  componentDidUpdate(previousProps, previousState) {
    if (
      JSON.stringify(previousState) !== JSON.stringify(this.state) ||
      JSON.stringify(previousProps) !== JSON.stringify(this.props)
    ) {
      if (!localStorage.getItem('cartId')) {
        return null;
      }
      getData(`cart/${localStorage.getItem('cartId')}`).then(res =>
        this.getCartItems(res.data.products)
      );
    }
  }

  getCartItems(cart) {
    const items = [];
    cart.forEach(item => items.push(getItem(item.id)));
    Promise.all(items).then(values => {
      for (let i = 0; i < cart.length; i++) {
        cart[i].images = values[i].data.images;
        cart[i].title = values[i].data.title;
        cart[i].brand = values[i].data.brand;
        cart[i].price = values[i].data.price;
      }
      this.setState({ cart });
      this.props.changeCart(localStorage.getItem('cartId') ? cart.length : 0);
    });
  }

  removeItem(item) {
    if (this.state.cart.length === 1) {
      this.props
        .changeCart(item.id, item.size, 0)
        .then(localStorage.removeItem('cartId'));
      this.setState({ cart: [] });
    } else {
      this.props
        .changeCart(item.id, item.size, 0)
        .then(res => this.getCartItems(res.data.products));
    }
  }

  render() {
    const { cart } = this.state;
    if (!cart) return null;
    return (
      <div className="hidden-panel__basket basket-dropped hidden-panel__basket_visible">
        {localStorage.getItem('cartId') ? (
          <div className="basket-dropped__title">В вашей корзине:</div>
        ) : null}
        <div
          className={`basket-dropped__product-list ${
            cart.length <= 3 ? 'product-list_less' : 'product-list'
          } `}
        >
          {localStorage.getItem('cartId') ? (
            cart.map((item, i) => {
              return (
                <div className="product-list__item" key={i}>
                  <a
                    className="product-list__pic"
                    style={{
                      backgroundImage: `url(${item.images[0]})`,
                      backgroundRepeat: 'no-repeat',
                      backgroundPosition: 'center center',
                      backgroundSize: 'cover',
                    }}
                  />
                  <Link
                    to={`/item/${item.id}`}
                    className="product-list__product"
                  >
                    {`${item.title}, (размер ${item.size}), ${item.amount} шт.`}
                  </Link>
                  <div className="product-list__fill" />
                  <div className="product-list__price">
                    {item.price * item.amount}
                    <i className="fa fa-rub" aria-hidden="true" />
                  </div>
                  <div className="product-list__delete">
                    <i
                      className="fa fa-times"
                      aria-hidden="true"
                      onClick={() => this.removeItem(item)}
                    />
                  </div>
                </div>
              );
            })
          ) : (
            <div className="product-list__item">
              <p>
                В корзине пока ничего нет. Не знаете, с чего начать? Посмотрите
                наши новинки!
              </p>
            </div>
          )}
        </div>
        {localStorage.getItem('cartId') ? (
          <Link
            className="basket-dropped__order-button"
            to="/order/"
          >
            Оформить заказ
          </Link>
        ) : null}
      </div>
    );
  }
}

export default Cart;



