import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import './css/style.css';
import './css/style-product-card.css';
import './css/style-order.css';
import './css/style-favorite.css';
import './css/style-catalogue.css';
import './css/normalize.css';
import './css/font-awesome.min.css';

import MainPage from './js/main_pages/MainPage';
import Catalog from './js/main_pages/Catalog';
import ProductCard from './js/main_pages/ProductCard';
import Favourite from './js/main_pages/Favourite';
import Order from './js/main_pages/Order.js';
import Header from './js/Components/Header.js';
import Footer from './js/Components/Footer.js';
import { getData, getItem } from './js/script';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cart: [],
      cartId: null,
      cartItems: '',
    };
  }

  componentDidMount() {
    if (!localStorage.getItem('cartId')) {
      return null;
    }
    getData(`cart/${localStorage.getItem('cartId')}`)
      .then(response =>
        this.setState({
          cart: response.data.products,
          cartItems: response.data.products.length,
          cartId: response.data.id,
        })
      )
      .catch(err => {
        if (this.state.cart.length === 0) {
          this.setState({
            cart: [],
            cartId: '',
            cartItems: '',
          });
        }
      });
  }

  componentWillReceiveProps(nextProps, nextState) {
    if (this.props !== nextProps || this.state !== nextState) {
      getData(`cart/${localStorage.getItem('cartId')}`)
        .then(response =>
          this.setState({
            cart: response.data.products,
            cartId: response.data.id,
            cartItems: response.data.products.length,
          })
        )
        .catch(err => {
          if (this.state.cart.length === 0) {
            this.setState({
              cart: [],
              cartId: '',
              cartItems: '',
            });
          }
        });
    }
  }

  changeCart = (id, size, amount) => {
    const json = { id, size, amount };

    let cart = localStorage.getItem('cartId');
    if (cart === null) cart = '';
    return getData(`cart/${cart}`, 'POST', json)
      .then(response => {
        localStorage.setItem('cartId', response.data.id);
        this.setState({
          cartId: response.data.id,
          cart: response.data.products,
          cartItems: response.data.products.length,
        });
        console.log(this.state);
      })
      .catch(err => console.log(err));
  };

  emptyCart = () => {
    this.setState({
      cart: [],
      cartId: '',
      cartItems: '',
    });
  };

  getProductCard(props) {
    return <ProductCard {...props} changeCart={this.changeCart} />;
  }

  render() {
    const { cart, cartId, cartItems, order } = this.state;
    if (!cart) return null;
    console.log(this.state);
    return (
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <div>
          <Header
            {...this.state}
            {...this.props}
            changeCart={this.changeCart}
          />
          <Switch>
            <Route path="/catalog/" component={Catalog} />
            <Route path="/catalog/:category" component={Catalog} />
            <Route
              path="/item/:id"
              render={(props, state) => this.getProductCard(props, state)}
            />
            <Route path="/favourite/" component={Favourite} />
            <Route path="/order/">
              <Order
                {...this.state}
                changeCart={this.changeCart}
                emptyCart={this.emptyCart}
              />
            </Route>
            <Route path="/" component={MainPage} />
          </Switch>
          <Footer />
        </div>
      </BrowserRouter>
    );
  }
}

export default App;



